
import XHeader from 'vux/src/components/x-header/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { mapGetters } from "vuex";

export default {
name: "Branch",
components: {
XHeader,
Search,
Group,
Cell,
XButton
},
mounted() {

this.searchResults = this.items;
},
methods: {
filterSearch(keyword) {
if (!!keyword) {
this.searchResults = this.items.filter(item => {
return item.user_login.indexOf(keyword) !== -1;
});
} else {
this.searchResults = this.items;
}
}
},
data() {
return {
items: [],
searchResults: [],
keyword: "",
jobnull: "无"
};
},

computed: {
...mapGetters(["userList"])
},

created() {
this.$store.dispatch("getuserList", {
department_id: this.$route.params.departmentid
});
},
activated() {
this.$store.dispatch("getuserList", {
department_id: this.$route.params.departmentid
});
},
deactivated() {
this.items = []
},

watch: {
userList: function(val) {
this.items = val;
},
items: function(val) {
this.searchResults = val;
}
}
};



